export type DialogsPropsType = {
    id: number
    name: string
    image: string
}
export type MessagesPropsType = {
    id: number
    message: string
    iTalk: boolean
}

let initialState = {
    dialogs: [
        {
            id: 1,
            name: 'Dimych',
            image: 'data:image/jpeg;base64,'
        },
        {
            id: 2,
            name: 'Andrey',
            image: 'data:image/jpeg;base64,'
        },
        {id: 3, name: 'Sveta', image: 'https://it-tehnik.ru/wp-content/uploads/matureman.png'},
        {
            id: 4,
            name: 'Sasha',
            image: 'data:image/jpeg;base64,'
        },
        {
            id: 5,
            name: 'Viktor',
            image: 'https://images.assetsdelivery.com/compings_v2/yupiramos/yupiramos1610/yupiramos161007352.jpg'
        },
        {
            id: 6,
            name: 'Valera',
            image: 'data:image/jpeg;base64,'
        }
    ] as DialogsPropsType[],
    messages: [
        {id: 1, message: 'Hi', iTalk: true},
        {id: 2, message: 'How is your it-kamasutra?', iTalk: false},
        {id: 3, message: 'Yo', iTalk: true},
        {id: 4, message: 'Yo', iTalk: true},
        {id: 5, message: 'Yo', iTalk: false}
    ] as MessagesPropsType[]
}

export const dialogReducer = (state: InitialStateDialogsType = initialState, action: ActionDialogPropsType): InitialStateDialogsType => {
    switch (action.type) {
        case 'ADD-MESSAGE':
            return {...state, messages: [...state.messages, {id: state.messages.length + 1, message: action.newMessage, iTalk: true}]}
        default:
            return state
    }
}

// actions
export const addNewMessageAC = (newMessage: string) => ({type: 'ADD-MESSAGE', newMessage} as const)

// types
export type InitialStateDialogsType = typeof initialState
export type ActionDialogPropsType = ReturnType<typeof addNewMessageAC>